export interface Endpoint {
  id: string;
  name: string;
  description: string;
  method: 'GET' | 'POST' | 'PUT' | 'DELETE';
  url: string;
  headers?: Record<string, string>;
  requestBody?: Record<string, unknown>;
  parameters?: Record<string, {
    type: string;
    description: string;
    required: boolean;
  }>;
  responses: {
    [statusCode: string]: {
      description: string;
      example: Record<string, unknown>;
    };
  };
  sampleCode?: {
    curl?: string;
    javascript?: string;
    python?: string;
  };
}

export interface ApiCategory {
  id: string;
  name: string;
  description: string;
  endpoints: Endpoint[];
}

